import { useEffect, useState} from "react"
import { Link, useParams } from "react-router-dom"

import { useCheckList } from "./CheckListContext"

const BreadCrumbs = (props) => {
    const [breadcrumbs, setBreadcrumbs] = useState([])
    const {hier, getRec} = useCheckList()
    const {scenarioTypeId, phaseIdx, subPhaseIdx} = useParams()

    useEffect(
        () => {

            const crumbs = []
            // const pth = location.pathname
            // console.log(pth, path, obj)
            // console.log(path.split('/'))
            // eslint-disable-next-line
            // let [_unused, _scenType, _scenTypeId, checklist, _phase, phaseIdx, _subphase, subPhaseIdx] = pth.split('/')
            // console.log(phaseIdx, subPhaseIdx)
            crumbs.push({href: `/scenario-type/${scenarioTypeId}/checklist`, label: "Sections"})
            if(!!phaseIdx) {
                const tmp = getRec(hier[phaseIdx])
                crumbs.push({href: `/scenario-type/${scenarioTypeId}/checklist/phase/${phaseIdx}`, label: tmp.label})
            }
            if (!!subPhaseIdx) {
                const tmp2 = getRec(hier[phaseIdx].subPhases[subPhaseIdx])
                crumbs.push({href: `/scenario-type/${scenarioTypeId}/checklist/phase/${phaseIdx}/subphase/${subPhaseIdx}`, label: tmp2.label })
            }
            setBreadcrumbs(crumbs)
        }, [getRec, hier, scenarioTypeId, phaseIdx, subPhaseIdx]
    )
    if (!breadcrumbs.length > 0) {
        return (<span></span>)
    }
    return (
        <nav className="breadcrumbs">
            {breadcrumbs.map((bc, idx, arr) =>
                <span key={idx}>
                    {idx === arr.length -1
                        ? <span className="breadcrumb"> {bc.label} </span>
                        : <><Link className="breadcrumb" to={bc.href} > {bc.label} </Link> &nbsp; &gt; &nbsp; </>
                    }
                </span>
            )}
        </nav>
    )
}

export default BreadCrumbs