import classnames from "classnames";
import ProblemsForm from '../Problems/ProblemsForm';
import { useScenarioEditor } from './ScenarioEditorContext';

const Problems = ({ active }) => {
    const { scenario, saveScenario } = useScenarioEditor();

    const saveChanges = async () => {
        await saveScenario(scenario);
    }

    return (
        <div className={classnames("scenario-form", { "scenario-view-hidden": !active })}>
            <div id="problems" className="info-wrapper">
                <ProblemsForm scenario={scenario} />
                <button type="button" onClick={saveChanges}>Save Changes</button>
            </div>
        </div>
    );
}

export default Problems;