import { useState } from "react";

export default function Contraindications({ med, update }) {
    const [expanded, setExpanded] = useState(false);

    const contraindications = [
        "active bleeding (internal or external)",
        "alcohol/drug use",
        "altered mental status",
        "central nervous system (CNS) depression",
        "chronic heart failure",
        "chronic lung disease",
        "gastrointestinal bleeding",
        "geriatric",
        "head injury",
        "hemophilia",
        "hepatic issues or failure",
        "hypersensitivity/allergy",
        "hypertension",
        "hypotension",
        "hypovolemia",
        "infant/toddler",
        "intoxication",
        "intracranial mass or bleeding",
        "pediatric",
        "peptic ulcers",
        "pregnancy",
        "recent dosage",
        "renal issues or failure",
        "respiratory depression",
        "SBP > 70-90 with uncontrolled bleeding",
        "suspected bowel obstruction",
        "suspected pneumothorax",
        "vascular bleeding",
    ];

    const changeView = () => setExpanded(e => !e);

    return (
        <div className="contraindications">
            <div className="dropdown" onClick={changeView}>Contraindications &nbsp;{!expanded ? "" : ""}</div>
            <div className={`checklist ${!expanded ? "hidden" : ""}`}>
                {contraindications.map((c, idx) => (
                    <div key={c} className="contraindication">
                        <label>
                            <input 
                                type="checkbox" 
                                onChange={update} 
                                data-type={"generic"}
                                data-index={idx}
                                data-key="contraindications"
                                name={c}
                                defaultChecked={med.contraindications.includes(c)}
                            />
                            {c}
                        </label>
                    </div>
                ))}
            </div>
        </div>
    )
}