import {useEffect, useState} from "react"

import {
    NavLink,
    Route,
    Switch,
    useParams,
    useRouteMatch,
} from "react-router-dom"

// import classnames from "classnames"

import { db } from "@meddbriefer/mdb-firebase"

import { ScenarioTypeContext } from "./ScenarioTypeContext"
import { useHeader } from "../HeaderContext"
import CheckList from "./CheckListEditor/CheckList"
import Medications from "./MedicationsEditor/Medications"

const COLLECTION_ID = "scenario-types"

const ScenarioType = (props) => {
    const [scenarioType, setScenarioType] = useState(null)
    const {scenarioTypeId} = useParams()
    const {path} = useRouteMatch()
    const {setTitle} = useHeader()

    const saveCheckList = (metaData, hierarchy) => {
        // filter out fields with 'undefined' as a value
        const cleanedMD = JSON.parse(JSON.stringify(metaData))

        setScenarioType(prevState => (
            {
                ...prevState,
                checklist: cleanedMD,
                phaseData: hierarchy
            }
        ))
        return db.collection(COLLECTION_ID).doc(scenarioTypeId).update({
            checklist: cleanedMD,
            phaseData: hierarchy
        })
        .then(() => true)
        .catch(error => {
            alert("an error occured saving scenario-type changes")
            console.log(error)
            return false
        })
    }

    const saveMedications = (data) => {
        setScenarioType(prevState => (
            {...prevState, medications: data}
        ))
        return db.collection(COLLECTION_ID).doc(scenarioTypeId).update({
            medications: data
        })
        .then(() => true)
        .catch(error => {
            console.log(error)
            return false
        })
    }

    const saveInterventions = (intvMetaData, intvMenus) => {
        setScenarioType(prevState => (
            {
                ...prevState,
                intvMetaData: intvMetaData,
                intvMenus: intvMenus
            }
        ))
        return db.collection(COLLECTION_ID).doc(scenarioTypeId).update({
            intvMetaData: intvMetaData,
            intvMenus: intvMenus
        })
        .then(() => true)
        .catch(error => {
            console.log(error)
            return false
        })
    }

    useEffect(() => {
        if (!!scenarioTypeId) {

            db.collection(COLLECTION_ID).doc(scenarioTypeId).get()
            .then(doc => {
                const data = doc.data()
                data.id = doc.id
                setTitle(`Scenario Type: ${data.name}`)
                setScenarioType(data)
            })
        }
    }, [scenarioTypeId, setTitle])


    if (!scenarioType) {
        return <div>Loading...</div>
    }
    return (
        <div className="scenario-type-editor">
            <nav id="tab-selector">
                <NavLink
                    to={`/scenario-type/${scenarioTypeId}/checklist`}
                    className="tab-button"
                    activeClassName="active-tab"
                >
                    Checklist
                </NavLink>
                <NavLink
                    to={`/scenario-type/${scenarioTypeId}/medications`}
                    className="tab-button"
                    activeClassName="active-tab"
                >
                    Medications
                </NavLink>
                <NavLink
                    to={`/scenario-type/${scenarioTypeId}/interventions`}
                    className="tab-button"
                    activeClassName="active-tab"
                >
                    Interventions
                </NavLink>
            </nav>
            <ScenarioTypeContext.Provider
                value={{
                    scenarioType: scenarioType,
                    saveCheckList: saveCheckList,
                    saveMedications: saveMedications,
                    saveInterventions: saveInterventions
                }}
            >
                <Switch>
                    <Route path={`${path}/checklist`} component={CheckList} />
                    <Route path={`${path}/medications`} component={Medications} />
                    <Route path={`${path}/interventions`}>
                        <div>Coming Soon...</div>
                    </Route>
                    <Route exact path={path}>
                        <div>Click on one of the tabs above to make changes.</div>
                    </Route>
                </Switch>
            </ScenarioTypeContext.Provider>
        </div>
    )
}

export default ScenarioType