import { useRef, useState, useEffect } from "react";
import { matchesLabel, matchesPhase } from "../../scripts/id-util";
import SearchResults from './SearchResults';

const Actions = ({ self: solution, display }) => {
    if (!solution.actions) solution.actions = [];

    const [num, setNum] = useState(0);

    useEffect(() => {
        setNum(solution.actions.length);
    }, [num, solution.actions]);

    return (
        <div className={`actions ${display ? "" : "hidden"}`}>
            <p className="mdb-block-heading">Actions:</p>
            {[...Array(num)].map((_, i) => (
                <ActionItem key={`action-${i}`} self={solution} num={i} actionState={[num, setNum]} />
            ))}
        <button type="button" onClick={() => setNum(num + 1)}>New Action</button>
        </div>
    );
}

export default Actions;

const ActionItem = ({ self: solution, num, actionState }) => {
    const [, setNum] = actionState;
    
    if (!solution.actions[num]) 
    solution.actions[num] = { id: "", phase: "", why: "", optional: false, minimal: false, critical: false };
    
    const self = solution.actions[num];
    
    const updateSelf = (e) => {
        e.target.type === "text"
            ? self[e.target.name] = e.target.value
            : self[e.target.name] = e.target.checked;
    }

    const deleteSelf = () => {
        solution.actions.splice(num, 1);
        setNum(solution.actions.length);
    }

    return (
        <div className="inputs">
            <Searchable 
                label={"ID:"}
                name={"id"}
                matchFunction={matchesLabel}
                self={self}
            />
            <Searchable 
                label={"Phase:"}
                name={"phase"}
                matchFunction={matchesPhase}
                self={self}
            />
            <label>
                Why:
                <input 
                    onChange={updateSelf} 
                    defaultValue={self.why}
                    name="why"
                    type="text" />
            </label>

            <label>
                Optional?
                <input
                    onChange={updateSelf}
                    defaultChecked={self.optional}
                    name="optional"
                    type="checkbox" />
            </label>
            <label>
                Minimal?
                <input
                    onChange={updateSelf}
                    defaultChecked={self.minimal}
                    name="minimal"
                    type="checkbox" />
            </label>
            <label>
                Critical?
                <input
                    onChange={updateSelf}
                    defaultChecked={self.critical}
                    name="critical"
                    type="checkbox" />
            </label>
        <button type="button" onClick={deleteSelf}>-</button>
        </div>
    );
}

const Searchable = ({ label, name, matchFunction, self }) => {
    const [query, setQuery] = useState();
    const idRef = useRef();

    const setValue = (e) => { 
        idRef.current.value = e.target.dataset.label; 
        self[`${name}-label`] = e.target.dataset.label;
        self[name] = e.target.dataset.id
        setQuery(null); 
    }

    return (
        <label>
            {label}
            <input 
                onChange={e => setQuery(e.target.value)}
                defaultValue={self[`${name}-label`]}
                name={name}
                type={"text"} 
                ref={idRef}
            />
            <SearchResults list={matchFunction(query)} setVal={setValue} />
        </label>
    )
};