import { db } from "@meddbriefer/mdb-firebase";

/**
 * Verify document doesn't already exist in database.
 * @param {string} collectionId
 * @param {string} documentId
 * @returns True if document with given ID doesn't exist yet.
 */
export const verifyDocument = async (collectionId, documentId) => {
    const set = await db.collection(collectionId).where("id", "==", `${documentId}`).get();
    return set.empty;
};

// returns true if combo of name and schemaVersion fields is unique
export const verifyDocNameUniqueness = async (collectionID, candidateName, dbNameField, schemaVersion) => {
    const snapshot = await db.collection(collectionID)
                    .where(dbNameField, "==", candidateName)
                    .where("schemaVersion", "==", schemaVersion)
                    .get()
    return snapshot.empty
}

// Upload document object to database
export const publishDocument = async (collectionId, documentObject, documentId) => {
    documentId
        ? await db.collection(collectionId).doc(documentId).set(documentObject)
        : await db.collection(collectionId).doc().set(documentObject);
};

// Update document with given id
export const updateDocument = async (collectionId, documentId, documentObject) => {
    const set = await db.collection(collectionId).where("id", "==", `${documentId}`).get();
    set.empty
        ? publishDocument(collectionId, documentObject)
        : await db.collection(collectionId).doc(set.docs[0].id).set(documentObject);
};

/**
 * Will save document, using auto-generated IDs if documentObject doesn't have
 * an 'id' attribute.
 * @param {string} collectionId
 * @param {object} documentObject
 */
export const saveDocument = async (collectionId, documentObject) => {
    const doc = (!documentObject.id)
                    ? db.collection(collectionId).doc()
                    : db.collection(collectionId).doc(documentObject.id);
    documentObject.id = doc.id;
    await doc.set(documentObject);
    return documentObject.id
}

/**
 * Query collection for document with id
 * @param {string} collectionId
 * @param {string} documentId
 * @returns {object} Object containing document data.
 */
export const getDocument = async (collectionId, documentId) => {
    const doc = await db.collection(collectionId).doc(documentId).get();
    return doc.data();
};

/**
 * Query collection for document with name
 * @param {string} collectionId
 * @param {string} documentName
 * @returns {object} Object containing document data.
 */
 export const getDocumentWithName = async (collectionId, documentName) => {
    const set = await db.collection(collectionId).where("name", "==", `${documentName}`).get();
    return set.docs[0]?.data();
};

/**
 * Delete document from collection
 * @param {string} collectionId
 * @param {string} documentId
 */
export const deleteDocument = async (collectionId, documentId) => {
    await db.collection(collectionId).doc(documentId).delete();
};

/**
 * Pull collection from database as array of document objects
 * @param {string} collectionId
 * @returns {object[]} An array containing all of the document in the collection.
 */
export const getCollection = async (collectionId) => {
    const collection = await db.collection(collectionId).get();
    return collection.docs?.map(item => item.data());
};