import { useEffect, useState } from "react"
import { useParams } from "react-router"
// import classnames from "classnames"

import AddCheckListItem from "./AddCheckListItem"
import BreadCrumbs from "./Breadcrumbs"
import CheckListItem from "./CheckListItem"
import ReadOnlyField from "./ReadOnlyField"
import SaveChecklistButton from "./SaveChecklistButton"
import TextInput from "./TextInput"

import { useCheckList } from "./CheckListContext"

const SubPhase = (props) => {
    const {phaseIdx, subPhaseIdx} = useParams()
    const [ctx, setCtx] = useState(null)
    const [rec, setRec] = useState(null)
    const { hier, getRec } = useCheckList()

    useEffect(
        () => {
            const _ctx = hier[phaseIdx].subPhases[subPhaseIdx]
            setCtx(_ctx)
        }, [phaseIdx, subPhaseIdx, hier]
    )

    useEffect(
        () => {
            !!ctx && setRec(getRec(ctx))
        }, [ctx, getRec]
    )


    if (!rec) {
        return (<div>loading...</div>)
    }
    return (
        <div className="sub-phase">
            <BreadCrumbs />
            <SaveChecklistButton />
            <div>
                <b>SubSection</b>
                <ReadOnlyField rec={rec} fld="id" label="ID" />
                <TextInput rec={rec} fld="label" label="Label" unique={true} />
                <fieldset className="top-level">
                    <legend>CheckList Items</legend>
                    {!ctx.items
                        ? <div>No checklist items yet.</div>
                        : <table>
                            <colgroup>
                                <col span="1" style={{ width: "5%" }} />
                                <col span="1" style={{ width: "10%" }} />
                                <col span="1" style={{ width: "20%" }} />
                                <col span="1" style={{ width: "40%" }} />
                                <col span="1" style={{ width: "20%" }} />
                                <col span="1" style={{ width: "5%" }} />
                            </colgroup>
                            <thead>
                                <tr>
                                    <th></th>
                                    <th>Type</th>
                                    <th>ID</th>
                                    <th>Label</th>
                                    <th>Abbr Label</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {ctx.items.map((item, i) =>
                                    <CheckListItem
                                        key={item.id}
                                        ctx={item}
                                        index={i}
                                        asTable={true}
                                    />
                                )}
                            </tbody>
                        </table>
                    }
                    <hr />
                    <AddCheckListItem
                        index={rec._ctx.lastIndex + 1}
                        phase={rec.phase}
                        subPhase={rec._ctx.id}
                        parentID={rec._ctx.id}
                    />
                </fieldset>
            </div>
            <SaveChecklistButton />
        </div>
    )

    // return (
    //     <div className="sub-phase">
    //         <BreadCrumbs />
    //         <div>
    //             <b>SubSection</b>
    //             <ReadOnlyField rec={rec} fld="id" label="ID" />
    //             <TextInput rec={rec} fld="label" label="Label" unique={true} />
    //             <fieldset className="top-level">
    //                 <legend>CheckList Items</legend>
    //                 {!ctx.items
    //                 ? <div> There aren't any checklist items yet </div>
    //                 : ctx.items?.map((item, i,) =>
    //                     <CheckListItem
    //                         key={item.id}
    //                         ctx={item}
    //                         index={i} />
    //                 )}
    //                 <hr />
    //                 <AddCheckListItem
    //                     index={rec._ctx.lastIndex + 1}
    //                     phase={rec.phase}
    //                     subPhase={rec._ctx.id}
    //                     parentID={rec._ctx.id}
    //                 />
    //             </fieldset>
    //         </div>
    //     </div>
    // )
}


export default SubPhase