import { useRef } from 'react';
import classnames from 'classnames';

import { useScenarioEditor } from './ScenarioEditorContext';
import { processForm } from './formUtils';

const Medications = ({ active }) => {
    const {scenario, saveScenario, scenarioType} = useScenarioEditor()
    const formEL = useRef(null)

    const getValue = (medication, field) => {
        const medications = scenario.correctMedicationDosages;
        if (undefined === medications ||
            undefined === medications[medication] ||
            undefined === medications[medication][field]) {
                return ""
            }
            return medications[medication][field]
        }
        
    const getChecked = (id, abbr) => {
        const medications = scenario.correctMedicationDosages;
        return medications[id].route[abbr];
    };

    const clickHandler = async (event) => {
        event.preventDefault()
        const form = formEL.current
        const newScen = processForm(scenario, form)
        // console.log(newScen.correctMedicationDosages)
        await saveScenario(newScen)
    }

    // Fill in default false for medication routes
    const routes = ["IV", "IO", "PO", "IM", "IN", "Inhalation"];
    for(const med of scenarioType.medications) {
        if (!scenario.correctMedicationDosages[med.id].route)
            scenario.correctMedicationDosages[med.id].route = {};

        for(const route of routes)
            if (scenario.correctMedicationDosages[med.id].route[route] == null)
                scenario.correctMedicationDosages[med.id].route[route] = false;
    }

    return (
        <form
            className={classnames("scenario-form", { "scenario-view-hidden": !active })}
            ref={formEL}
        >
            <button className="save-changes" type="submit" onClick={clickHandler}>Save Changes</button>

            <div className="info-wrapper" id="medications">
                <h1>Medication Dosages</h1>

                <div className="meds-wrapper">
                    <table>
                        <thead>
                            <tr>
                                <th>Medication</th>
                                <th>Initial Dosage</th>
                                <th>Max Dosage</th>
                                <th>Repeat Dosage</th>
                                <th>Repeat Frequency</th>
                                <th>IV</th>
                                <th>IO</th>
                                <th>PO</th>
                                <th>IM</th>
                                <th>IN</th>
                                <th>Inhalation</th>
                            </tr>
                        </thead>
                        <tbody>
                            {scenarioType.medications.map(med =>
                                <tr key={`${med.id}-row`}>
                                    <th>{med.label}</th>
                                    <td>
                                        <input type="text"
                                            id={`correctMedicationDosages-${med.id}-initialDosage`}
                                            defaultValue={getValue(med.id, "initialDosage")}
                                        />
                                    </td>
                                    <td>
                                        <input type="text"
                                            id={`correctMedicationDosages-${med.id}-maxDosage`}
                                            defaultValue={getValue(med.id, "maxDosage")}
                                        />
                                    </td>
                                    <td>
                                        <input type="text"
                                            id={`correctMedicationDosages-${med.id}-repeatDosage`}
                                            defaultValue={getValue(med.id, "repeatDosage")}
                                        />
                                    </td>
                                    <td>
                                        <input type="text"
                                            id={`correctMedicationDosages-${med.id}-repeatFrequency`}
                                            defaultValue={getValue(med.id, "repeatFrequency")}
                                        />
                                    </td>
                                    <td>
                                        <input type="checkbox"
                                            id={`correctMedicationDosages-${med.id}-route-IV`}
                                            data-isadministrationtype={true}
                                            defaultChecked={getChecked(med.id, "IV")}
                                            value="IV"
                                            />
                                    </td>
                                    <td>
                                        <input type="checkbox"
                                            id={`correctMedicationDosages-${med.id}-route-IO`}
                                            data-isadministrationtype={true}
                                            defaultChecked={getChecked(med.id, "IO")}
                                            value="IO"
                                            />
                                    </td>
                                    <td>
                                        <input type="checkbox"
                                            id={`correctMedicationDosages-${med.id}-route-PO`}
                                            data-isadministrationtype={true}
                                            defaultChecked={getChecked(med.id, "PO")}
                                            value="PO"
                                            />
                                    </td>
                                    <td>
                                        <input type="checkbox"
                                            id={`correctMedicationDosages-${med.id}-route-IM`}
                                            data-isadministrationtype={true}
                                            defaultChecked={getChecked(med.id, "IM")}
                                            value="IM"
                                            />
                                    </td>
                                    <td>
                                        <input type="checkbox"
                                            id={`correctMedicationDosages-${med.id}-route-IN`}
                                            data-isadministrationtype={true}
                                            defaultChecked={getChecked(med.id, "IN")}
                                            value="IN"
                                            />
                                    </td>
                                    <td>
                                        <input type="checkbox"
                                            id={`correctMedicationDosages-${med.id}-route-Inhalation`}
                                            data-isadministrationtype={true}
                                            defaultChecked={getChecked(med.id, "Inhalation")}
                                            value="Inhalation"
                                        />
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>
            </div>
            <button className="save-changes" type="submit" onClick={clickHandler}>Save Changes</button>

        </form>
    )
}

export default Medications;