import { useEffect, useState } from "react"

import { useHistory } from "react-router-dom";

import classnames from "classnames"

import { db } from "@meddbriefer/mdb-firebase"
import { getScenarioType } from "@meddbriefer/scenario-data/index.js";

import { saveDocument, deleteDocument } from "../../scripts/connect-firebase";

import {INTV_META_DATA } from "@meddbriefer/scenario-data/traumaScenarioV2MetaData.js"
import { INTV_FORMS, INTV_MENUS, SYSTEM_INTVS} from "@meddbriefer/scenario-data/traumaScenarioV2.js"


const deepCopy = (data) => {
    // console.log(data)
    const copied = JSON.parse(JSON.stringify(data))
    // console.log("success")
    return copied
}


const mergeData = (scenarioData, scenarioTypeData, actionsData = {}) => {
    const scenario = deepCopy(scenarioData)

    scenario.checkListMetaData = deepCopy(scenarioTypeData.checkListMetaData)
    scenario.phaseData = deepCopy(scenarioTypeData.phaseData)
    // scenario.hierarchy = deepCopy(scenarioTypeData.hierarchy)
    scenario.medications = deepCopy(scenarioTypeData.medications)
    // info still maintained in files
    scenario.intvMetaData = deepCopy(INTV_META_DATA)
    scenario.interventionMenus = deepCopy(INTV_MENUS)
    scenario.systemInterventions = deepCopy(SYSTEM_INTVS)
    // FIXME: scenario-specific substitutions in prompt answers still need to be performed
    scenario.interventionForms = deepCopy(INTV_FORMS)
    // FIXME: this will be replaced by scenarioType.hierarchy
    // scenario.phaseData = deepCopy(PHASE_DATA)
    const associations = deepCopy(actionsData?.associations || {})
    // merge checklistCorrectness into associations so that there is a single
    // location to lookup correct answers for an id, be it an intervention prompt, decision, or assessment w/options
    Object.entries(scenario.checkListCorrectness).forEach(([key, value]) => {
        associations[key] = value
    })
    scenario.associations = associations
    scenario.problems = deepCopy(actionsData?.problems || [])
    return scenario
}


const PublishScenario = (props) => {
    const {scenarioId} = props.match.params
    const history = useHistory()
    const [versionName, setVersionName] = useState("")
    const [error, setError] = useState(null)
    const [mergedData, setMergedData] = useState(null)
    const [versions, setVersions] = useState([])

    const changeFileName = (event) => {
        setVersionName(event.target.value)
    }

    const unpublish = async (publishedScenarioID) => {
        console.log(publishedScenarioID)
        deleteDocument("published-scenarios", publishedScenarioID)
        .then(
                loadPublishedScenarioVersions(mergedData.name)
        )
    }

    const publishWithNewName = async (event) => {
        event.preventDefault()
        if ("" === versionName) {
            setError("Please provide a name to publish this version of the scenario data as.")
        } else {
            setError(null)
            await publishData(versionName)
            setVersionName("")
        }
    }

    const republishAs = (versionName) => {
        const existingVersion = versions.find(ver => ver.version === versionName)
        if (window.confirm(`Are you sure you wish to replace the data currently published as "${versionName}"?`)) {
            publishData(versionName, existingVersion.id)
        }
    }

    const publishData = async (versionName, versionID = null) => {
        const doc = Object.assign({}, mergedData, { version: versionName, datePublished: new Date().toLocaleString() })
        delete doc.id
        if (!!versionID) {
            doc.id = versionID
        }
        await saveDocument("published-scenarios", doc)
        await loadPublishedScenarioVersions(doc.name)
        alert(`scenario data published as ${versionName}`)
        history.push("/scenarios")
    }

    const loadPublishedScenarioVersions = async (scenarioName) => {
        let variants = []
        const scenVersions = await db.collection("published-scenarios")
            .where("name", "==", scenarioName)
            .where("schemaVersion", "==", "V2")
            .get()
        if (scenVersions.size > 0) {
            variants = scenVersions.docs.map(doc => {
                // data is HUGE, we only need: id, name, datePublished, and version fields
                const { id, name, datePublished, version } = doc.data()
                return { id, name, datePublished, version }
            })
        }
        setVersions(variants)
    }

    useEffect(() => {
        (async () => {
            const scen = await db.collection("scenarios").doc(scenarioId).get()
            const scenData = scen.data()
            const scenTypeData = await getScenarioType(db, scenData.scenarioType, scenData.schemaVersion)
            let actions = { problems: [], associations: {} }
            const actionsQuery = await db.collection("problem-solutions")
                                     .where("scenarioName", "==", scenData.name)
                                     .where("schemaVersion", "==", scenData.schemaVersion)
                                     .limit(1)
                                     .get()
            if (actionsQuery.size === 1) {
                actions = actionsQuery.docs[0].data()
            }
            const merged = mergeData(scenData, scenTypeData, actions)
            setMergedData(merged)
            loadPublishedScenarioVersions(scenData.name)
        })()
    }, [scenarioId])

    if (!mergedData) {
        return <div>Loading...</div>
    }
    return (
        <div>
            <h1>Publish Scenario: "{mergedData.name}" (type: "{mergedData.scenarioType}") </h1>
            <h4>First verify that the following pieces of information is ready for publication:</h4>
            <ul>
                <li>findings for assessment steps are specified</li>
                <li>problems and solutions are specified: <b>{(!!mergedData.problems).toString()}</b></li>
                <li>answers to intervention prompts are specified</li>
                <li>medication-related info is specified</li>
            </ul>

            <h4>Published versions of this scenario:</h4>
            <table style={{
                width: "80%"
            }}>
                <thead>
                    <tr>
                        <th>Version</th>
                        <th>Date Published</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                {versions.map(item =>
                    <tr key={item.version}>
                        <td>{item.version}</td>
                        <td>{item.datePublished}</td>
                        <td>
                            <button onClick={() => republishAs(item.version)}> Republish using this name </button>
                            <button onClick={() => unpublish(item.id)}>Unpublish</button>
                        </td>
                    </tr>
                )}
                </tbody>
            </table>

            <label> Publish using name:
                <input
                    type="text"
                    placeholder="Name for this version"
                    value={versionName}
                    onChange={changeFileName}
                />

            </label>
            <button onClick={publishWithNewName}>Publish</button>

            <div className={classnames({"hidden": !error })}>{error}</div>
        </div>
    )
}

export default PublishScenario