export const getScenarios = (db, scenarioType, schemaVersion) => {
    const query = db.collection("scenarios")
             .where("scenarioType", "==", scenarioType)
             .where("schemaVersion", "==", schemaVersion)
    return query.get()
        .then(snapShot => {
            const scenarios = []
            if (snapShot.size > 0) {
                snapShot.docs.forEach(doc => {
                    const data = doc.data()
                    scenarios.push({
                        id: data.id,
                        name: data.name,
                        scenarioType: data.scenarioType,
                        schemaVersion: data.schemaVersion
                    })
                })
            }
            return scenarios
        })
}
export const getPublishedScenarioByID = (db, scenarioID) => {
    return db.collection("published-scenarios").doc(scenarioID).get().then(doc => doc.data())
}

export const getScenarioByID = (db, scenarioID) => {
    return db.collection("scenarios").doc(scenarioID).get()
        .then(doc => doc.data())
        // .then(scenData => mergeData(db, scenData))
}

export const getScenarioByName = (db, scenarioName, schemaVersion) => {
    return db.collection("scenarios")
             .where("name", "==", scenarioName)
             .where("schemaVersion", "==", schemaVersion)
             .limit(1)
             .get()
            .then(snapShot => {
                if (snapShot.size === 1) {
                    return snapShot.docs[0].data()
                } else {
                    throw new Error("document not found")
                }
            })
            // .then(scenData => mergeData(db, scenData))
            // .catch(error => error)

}

export const getScenarioType = (db, scenarioType, schemaVersion) => {
    return db.collection("scenario-types")
            .where("name", "==", scenarioType)
            .where("schemaVersion", "==", schemaVersion)
            .limit(1)
            .get()
            .then(snapShot => {
                if (snapShot.size === 1) {
                    return fixupScenarioTypeData(snapShot.docs[0].data())
                } else {
                    throw new Error("document not found")
                }
            })
}

//uses the new collection
const getRequiredActions = (db, scenario) => {
    return db.collection("problem-solutions")
    // return db.collection("scenario-required-actions")
        .where("scenarioName", "==", scenario.name)
        .where("schemaVersion", "==", scenario.schemaVersion)
        .limit(1)
        .get()
        .then(snapShot => {
            if (snapShot.size === 1) {
                const doc = snapShot.docs[0].data()
                return {
                    problems: doc.problems,
                    // criticalActions: doc.criticalActions,
                    associations: doc.associations
                }
            } else {
                return {
                    problems: [],
                    // criticalActions: [],
                    associations: {}
                }
            }
        })
}

const mergeAllData = (scenData, requiredActions, scenarioTypeData) => {
    const scenario = deepCopy(scenData)
    scenario.associations    = deepCopy(requiredActions.associations)
    //scenario.criticalActions = deepCopy(requiredActions.criticalActions)
    scenario.problems = deepCopy(requiredActions.problems)

    scenario.checkListMetaData = deepCopy(scenarioTypeData.checkListMetaData)
    scenario.hierarchy         = deepCopy(scenarioTypeData.hierarchy)
    scenario.medications       = deepCopy(scenarioTypeData.medications)

    // info still maintained in files
    scenario.intvMetaData        = deepCopy(INTV_META_DATA)
    scenario.interventionMenus   = deepCopy(INTV_MENUS)
    scenario.systemInterventions = deepCopy(SYSTEM_INTVS)
    // FIXME: scenario-specific substitutions in prompt answers still need to be performed
    scenario.interventionForms = deepCopy(INTV_FORMS)
    // this will be replaced by scenarioType.hierarchy
    scenario.phaseData = deepCopy(PHASE_DATA)
    return scenario
}

const fixupScenarioTypeData = (scenarioTypeData) => {
    // TEMPORARY HACK - due to subphase items not having a "phase" attribute. track subPhases "phase"
    const subPhase2Phase = {}
    const checklistMetaData = scenarioTypeData.checklist.map(rec => {
        if ("sub-phase" === rec.type) {
            subPhase2Phase[rec.id] = rec.phase
        }
        delete rec.relation
        delete rec.items
        delete rec.subPhases
        if ("sub-phase" in rec) {
            const sp = rec["sub-phase"]
            delete rec["sub-phase"]
            rec.subPhase = sp
            rec.phase = subPhase2Phase[sp]
        }
        return rec
    })
    delete scenarioTypeData.checklist
    scenarioTypeData.checkListMetaData = checklistMetaData
    return scenarioTypeData
}

const mergeData = async (db, scenData) => {
    const requiredActions = await getRequiredActions(db, scenData)
    const scenTypeData = await getScenarioType(db, scenData.scenarioType, scenData.schemaVersion)
    return mergeAllData(scenData, requiredActions, scenTypeData)
}


export const getPromptAndOptions = (scenario, promptID) => {
    const prompt = scenario.intvMetaData.find(rec => rec.id === promptID)
    const options = scenario.intvMetaData.filter(rec => rec.promptID === promptID)
    prompt.options = options.map(opt => {
        // not sure this is neccessary anymore since we have direct access to this now
        // as compared to being preprocessed
        opt.prompt = prompt.label
        return opt
    })
    return prompt
}

