import { INTV_META_DATA, /*CHECKLIST_META_DATA*/ } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData';
import { MDB_TYPES as MDB } from '@meddbriefer/scenario-data/constants.js';
import { getDocumentWithName } from './connect-firebase';

let phases = [];
let assessments = [];
let vitals = [
    { label: "Blood pressure", id: "BP" },
    { label: "Pulse", id: "P" },
    { label: "Respirations", id: "R" },
    { label: "Skin condition", id: "Skin" },
    { label: "Sp02", id: "Sp02" },
    { label: "Etc02", id: "ETC02" },
    { label: "GCS", id: "GCS" },
    { label: "Glucose", id: "Glucose" },
    { label: "Pain", id: "Pain" },
    { label: "Temperature", id: "Temp" },
];

let intvIdList = INTV_META_DATA
                .filter(intv => intv.type === 'intervention')
                .sort((one, two) => {
                    if (one.label.localeCompare(two.label) < 0) return -1;
                    if (one.label.localeCompare(two.label) > 0) return 1;
                    return 0;
                });

// Load in lists from database
getDocumentWithName("scenario-types", "trauma")
    .then(doc => {
        assessments = doc.checklist.filter(e => e.type === MDB.ASSESS);
        phases = doc.checklist.filter(item => item.type === MDB.PHASE || item.type === MDB.SUB_PHASE);
    })
    .catch(console.error);

export const getLabel = (id, type) => {
    switch (type) {
        case "assessments":
            return assessments.find(e => e.id === id);
        case "phases":
            return phases.find(e => e.id === id);
        case "vitals":
            return vitals.find(e => e.id === id);
        case "interventions":
            return intvIdList.find(e => e.id === id);
        default:
            return null;
    }
}

export const matchesVital = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let vital of vitals)
        if (vital.label.toLowerCase().startsWith(str.toLowerCase()))
            matchArr.push(vital);
    
    return matchArr.length > 0 ? matchArr : null;
};

export const matchesPhase = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of phases) 
        if (item.label?.toLowerCase().includes(str.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}

export const matchesLabel = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of intvIdList) 
        if (item.label?.toLowerCase().includes(str.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}

